import type {
    BaseViz,
    ItemShouldAnnounceChangeFunction,
} from '@splunk/dashboard-types';

/**
 * Add a property to the Viz class that determines when the changes in component warrant an announcement
 */
export default function withCustomShouldAnnounceChange<T extends BaseViz>(
    Viz: T,
    {
        shouldAnnounceChange,
    }: { shouldAnnounceChange: ItemShouldAnnounceChangeFunction }
): T {
    Viz.shouldAnnounceChange = shouldAnnounceChange;
    return Viz;
}
